import React from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ProductCard from './ProductCard'

const Recommendations = () => {
    return (
        <section className='mb-8'>
            <h2> </h2>
            <Row xs={1} sm={2} md={3} xl={4} className='gy-5 gx-4 g-xxl-5'>
                <Col>
                    <ProductCard inStock={true} title={'EC -     + '} imgUrl={'imgs/products/EC -     + .jpg'} />
                </Col>
                <Col>
                    <ProductCard inStock={true} title={'SS -     +  '} imgUrl={'imgs/products/SS -     +  .jpg'} />
                </Col>
                <Col>
                    <ProductCard inStock={true} title={'EC -     + '} imgUrl={'imgs/products/EC -     + .jpg'} />
                </Col>
                <Col>
                    <ProductCard inStock={false} title={'EC -     + '} imgUrl={'imgs/products/EC -     + .jpg'} />
                </Col>
            </Row>
        </section>
    );
};

export default Recommendations;