import React from 'react'
import Banner from '../components/Banner'

const Home = () => {
    return (
        <main>
            <Banner link={'/category/product'} main={true} title={' FR  '} imgUrl={'imgs/products/ FR  .jpg'}/>

            <Banner link={'/category/product'} main={false} title={'HA '} imgUrl={'imgs/products/HA -      .jpg'}/>
            <Banner link={'/category'} main={false} title={'MATE '} imgUrl={'imgs/products/SS -     +  .jpg'}/>
            <Banner link={'/category'} main={false} title={' '} imgUrl={'imgs/products/MM -   - + 2  .jpg'}/>
            <Banner link={'/category'} main={false} title={'EC '} imgUrl={'imgs/products/EC -     + .jpg'}/>
        </main>
    );
};

export default Home;